.title-text {
  position: relative;
}

.cyber-title-text {
  margin: 0;
  color: #ffee08;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 1.6rem;
  font-family: "Orbitron";
  font-weight: 900;
  letter-spacing: -1px;
  padding: 2rem;
  white-space: nowrap;
}

.cyber-title-text:after,
.cyber-title-text:before {
  position: absolute;
  content: attr(data-text);
  padding: 2rem;
  top: 0;
  left: 0;
  color: #ffee08;
  overflow: hidden;
  white-space: nowrap;
}

.cyber-title-text:before {
  left: 3px;
  text-shadow: -3px 2px #87cefa;
  animation: glitch-1 3s ease-in infinite reverse;
}

.cyber-title-text:after {
  left: -3px;
  text-shadow: -5px 2px #fff;
  animation: glitch-2 3s ease-in-out infinite reverse;
}

