  background: transparent;
  font-weight: 700;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  font-family: "Orbitron", sans-serif !important;
}

@keyframes slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-10px);
  }
}

